
h1, h2, h3, h4, h5, h6 {
  padding: 0;
  margin: 0;
}
body {
  background-color: rgb(60, 60, 60);
  color: #e2e2e2;
  font-family: 'IBM Plex Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  padding: 0;
  margin: 0;
}
button {
  background-color: rgb(70, 70, 70);
  color: white;
  border: 0;
  margin-top: 1vh;
  border-radius: 1vh;
  padding: 1vh;
}
button:hover {
  -webkit-transition: color 1s;
  transition: color 1s;
  background-color: rgb(124, 123, 123);
}
::-webkit-scrollbar {
  width: 11px;
  height: 11px;
}
::-webkit-scrollbar-thumb {
  background: #333;
  border: 1px solid #222;
  border-radius: 50px;
}
::-webkit-scrollbar-track {
  background: #666;
  border: none;
  border-radius: 53px;
}
::-webkit-scrollbar-corner {
  background: transparent;
}
.flex-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#app-header {
  width: 100%;
  height: 6vh;
  line-height: 6vh;
}
.app-container {
  background-color: rgb(42, 42, 42);
  border-radius: 1vh;
  margin: 1vh;
  overflow-y: auto;
}
.midi-input-select {
  max-width: 100vh;
  padding: 5vh;  
  margin: auto;  
  margin-top: 2vh;
}
.option-light {
  background-color: rgb(70, 70, 70);
  border-radius: 1vh;
  padding: 0 1vh;
  margin-top: 1vh;
}
.option-light.selected {
  background-color: rgb(124, 123, 123);
}

.synthetizers-list[data-v-21aeb5cc] {
  width: 25%;
  height: 65vh;
  margin: 1vh;
  padding: 2vh;
}


.synth-graph[data-v-441f8baa] {
  height: 65vh;
  width: 35%;
  margin: 0;
  padding: 2vh;
  margin-top: 1vh;
}

.synth-details {
  margin: 1vh;
  width: 40%;
  padding: 1vh
}

.visualizer-container {
  margin: 0 1vh;
  padding: 1vh;
  width: 99%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.keys {
  margin: auto;
  width: 1150px;
  text-align: left;
}



























































































































